import { createStore } from "vuex";

export default createStore({
  state: { // [변수들의 집합]
    name: "이름"
  },
  getters: { // [state의 변수들을 get 호출]
    getUserInfo(state){
      return "이름 : " + state.name;
    }
  },
  mutations: { // [변수들을 조작하는 함수들]
    setUserInfo(state, value){
      state.name = value;
    }
  },
  actions: { // [비동기 처리를 하는 함수들]
    
  },
  modules: {},
});
