<template>
  <div class="container">
    <div class="search-bar">
      <input type="text" v-model="filterCategory" @click="e => e.target.classList.toggle('active')">
      <div class="list">
        <label><input type="radio" v-model="filterCategory" value="전체" /> 전체</label>
        <label><input type="radio" v-model="filterCategory" value="sideDished" /> 밑반찬</label>
        <label><input type="radio" v-model="filterCategory" value="mainSideDished" /> 메인반찬</label>
        <label><input type="radio" v-model="filterCategory" value="krSoup" /> 국/탕</label>
        <label><input type="radio" v-model="filterCategory" value="krstew" /> 찌개</label>
        <label><input type="radio" v-model="filterCategory" value="dessert" /> 디저트</label>
        <label><input type="radio" v-model="filterCategory" value="noodleDumpling" /> 면/만두</label>
        <label><input type="radio" v-model="filterCategory" value="ricePorridgeRiceCake" /> 밥/죽/떡</label>
        <label><input type="radio" v-model="filterCategory" value="fusion" /> 퓨전</label>
        <label><input type="radio" v-model="filterCategory" value="kimchiSaltedPaste" /> 김치/젓갈/장류</label>
        <label><input type="radio" v-model="filterCategory" value="seasoningSauceJam" /> 양념/소스/잼</label>
        <label><input type="radio" v-model="filterCategory" value="westernFood" /> 양식</label>
        <label><input type="radio" v-model="filterCategory" value="salad" /> 샐러드</label>
        <label><input type="radio" v-model="filterCategory" value="soup" /> 스프</label>
        <label><input type="radio" v-model="filterCategory" value="bread" /> 빵</label>
        <label><input type="radio" v-model="filterCategory" value="snacks" /> 과자</label>
        <label><input type="radio" v-model="filterCategory" value="teaDrinksAlcohol" /> 차/음료/술</label>
      </div>
    </div>
    <ul class="mainList">
      <li v-for="(item, idx) in items" :class="item.typeList" :key="item.idx">
        <div class="img">
          <img :src="require('@/assets/imgs/dish/' + item.imgUrl)" :alt="item.dishName" @error="replaceByDefault">
          <a :href="item.href" target="_blank" class="ico-recipe">바로가기</a>
        </div>
        <div class="info">
          <a :href="item.page" class="tit"><span class="byType">{{ item.byType }}</span>{{ item.dishName }}</a>
          <div class="by">by. <b>{{ item.dishBy }}</b></div>
        </div>
      </li>
    </ul>
  </div>
</template>
반갑특갑
<script>
import img from "@/assets/imgs/noImg.svg";
import data from "@/assets/jsons/dishLists.json";

const items = data;


export default {
  el: "#items",
  methods: {
    replaceByDefault(e) {
      e.target.src = img
    },
  },
  data() {
    return {
      items,
      filterCategory: "전체",
      selected: "전체",
      isActive: false,
    }
  }, //data
  computed: {
    items: function() {
			var vm = this;
			var typeList = vm.filterCategory;
			
			if(typeList === "전체") {
				return vm.items;
			} else {
      return vm.items.filter(function(item) {
          return item.typeList === typeList;
        });
      }
    },
  }, //computed
} //export default
</script>
